.filter_box {
    display: flex;
    div {
        display: flex;
        align-items: center;
        margin-right: 16px;
        input, select {
            width: 90px;
            margin-left: 4px;
        }
        span {
            font-size: 12px;
        }
    }
}
.table {
    width: 100%;
    // border: 1px solid grey;
    padding: 16px 0;
    .label{
        background-color: #d9d9d9;
        padding: 7px;
        margin: 0 16px 0 0;
    }
}
input, select {
    margin: 0 16px;
    width: 220px;
}
.row {
    display: flex;
    width: 100%
}
.item {
    display: flex; 
    width: 50%;
    border: 1px solid #c7c7c7;
}
.label {
    width: 150px
}
button {
    background-color: black;
    color: white;
    padding: 4px 8px;
    border-radius: 2px;
    min-width: 80px;
    margin-right: 8px;
}
.btn {
    display: flex;
    justify-content: space-between;
    margin-top: 16px;
    margin-bottom: 16px;
}

.content-container{
    padding: 2rem 16px;
    .content{
        display: flex;
        align-items: center;
        p{
            font-size: 16px;
            min-width: 200px;
        }
        .radio-group{
            label{
                font-size: 12px;
                margin-right: 16px;
            }
            input{
                width: 30px;
            }
            .input{
                width: 30px;
            }
        }
        .input-group{
            display: flex;
            align-items: center;
            label{
                font-size: 12px;
                margin-right: 16px;
            }
            input{
                width: 250px;
            }
        }
    }
}

.operations{
    border-top: 1px solid black;
}